@use "base.scss" as *;

@media (max-width: 952px) and (min-width: 500px) {
  #main-nav {
    .navbar__inner {
      ul {
        display: none;
      }
      .mobile-toggle {
        display: block;
      }
    }
  }

  .mobile-nav {
    display: block;
    position: fixed;
    inset: 6rem 0 0 0;
    z-index: 99;
    background-color: $overlay-color;
    transform: translateX(-100%);
    transition: transform 0.2s;

    ul {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      height: 75%;
      padding-top: 10%;
      font-size: 2rem;
    }
  }

  .show-nav {
    transform: translateX(0);
  }

  #header-home {
    min-height: 90vh;

    .container {
      padding: 0 8rem;

      .header-content {
        padding-top: 40%;

        &__text {
          max-width: unset;
        }
      }
    }
  }
}

@media (hover: none) {
  a:hover {
    color: inherit;
    text-shadow: none;
    transfrom: translateY(0);
  }
}
